module.exports = "/*\n * @Author: honghong\n * @LastEditors: honghong\n * @Description: 图片查看组件的样式\n * @email: 3300536651@qq.com\n * @Date: 2019-04-16 15:57:43\n * @LastEditTime: 2019-04-17 10:45:34\n */\n.imgview-wrap {\n  width: 100%;\n  height: 100%;\n  position: relative;\n  padding-top: 40px;\n}\n.imgview-content {\n  position: relative;\n  overflow: auto;\n  overflow-y: auto;\n  width: 100%;\n  height: 100%;\n  padding: 10px;\n  background-color: #ffffff;\n  border: 2px solid #ddd;\n}\n.imgview-content .imgview-detail {\n  width: 100%;\n  height: auto;\n  position: absolute;\n  left: 0;\n  top: 0;\n  transition: -webkit-transform 0.3s;\n  transition: transform 0.3s;\n  transition: transform 0.3s, -webkit-transform 0.3s;\n  cursor: pointer;\n  max-width: none;\n}\n.imgview-wrap .imgview-toolbar {\n  width: 100%;\n  height: 50px;\n  background-color: #f6f6f6;\n  position: absolute;\n  left: 0;\n  top: 0;\n  padding: 5px;\n  text-align: right;\n}\n.imgview-wrap .toolbar-action {\n  margin-right: 5px;\n  width: 40px;\n  height: 40px;\n  line-height: 40px;\n  display: inline-block;\n  padding: 0;\n  font-size: 18px;\n  border-radius: 50%;\n  color: #ffffff;\n  background-color: #1890ff;\n  border-color: #1890ff;\n  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.12);\n  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);\n  text-align: center;\n  outline: none;\n  cursor: pointer;\n}\n.imgview-wrap .toolbar-action:hover {\n  color: #fff;\n  background-color: #40a9ff;\n  border-color: #40a9ff;\n}\n.imgview-wrap .toolbar-action i {\n  font-size: 16px;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"